import React, { useEffect, useRef, useState} from 'react';
import * as d3 from 'd3';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css'; // Importar CSS do Leaflet
import { Bar } from 'react-chartjs-2';
import { Modal } from 'react-bootstrap'; // Usando react-bootstrap para o modal


let geojsonLayer; // Variável para armazenar o layer do GeoJSON
// Função para remover acentuação, espaços e converter para minúsculas
const normalizeMunicipioName = (name) => {
    return name
        .normalize('NFD') // Normaliza o texto para decompô-lo
        .replace(/[\u0300-\u036f]/g, '') // Remove os acentos
        .toLowerCase() // Converte para minúsculas
        .replace(/ /g, '_'); // Substitui espaços por underscores
};

const MapComponent = ({ municipio }) => {
    const mapRef = useRef(null);
    const [showModal, setShowModal] = useState(false); // Estado do modal
    const [chartData, setChartData] = useState(null);  // Estado para dados do gráfico
    const [chartOptions, setChartOptions] = useState(null);
    const [featureId, setFeatureId] = useState(null);  // Armazena o ID da feature clicada

    useEffect(() => {
        // Inicializar o mapa
        console.log('Container do mapa:', mapRef.current);

        const map = L.map(mapRef.current, {
            center: [-23.5, -51.5],
            zoom: 7,
            scrollWheelZoom: true,
            zoomControl: true,
        });

        L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
            attribution: '© OpenStreetMap contributors',
        }).addTo(map);

        // Função para determinar a cor com base no valor de num_area
        const getColor = (num_area) => {
            return num_area > 1000 ? '#800026' :
                   num_area > 500 ? '#BD0026' :
                   num_area > 200 ? '#E31A1C' :
                   num_area > 100 ? '#FC4E2A' :
                   num_area > 50  ? '#FD8D3C' :
                                    '#FED976';
        };
        const getGraphColor = (classe) => {
            console.log('getgcolor: ', classe)
            return classe === "'Floresta'" ? '#32a65e':classe === "'Formação Florestal'" ? '#1f8d49':classe === "'Formação Savânica'" ? '#7dc975':classe === "'Mangue'" ? '#04381d':classe === "'Floresta Alagável (beta)'" ? '#026975':classe === "'Restinga Arbórea'" ? '#02d659':classe === "'Formação Natural não Florestal'" ? '#ad975a':classe === "'Campo Alagado e Área'" ? '#519799':classe === "'Formação Campestre'" ? '#d6bc74':classe === "'Apicum'" ? '#fc8114':classe === "'Afloramento Rochoso'" ? '#ffaa5f':classe === "'Restinga Herbácea'" ? '#ad5100':classe === "'Outras Formações não Florestais'" ? '#d89f5c':classe === "'Agropecuária'" ? '#FFFFB2':classe === "'Pastagem'" ? '#edde8e':classe === "'Agricultura'" ? '#E974ED':classe === "'Lavoura Temporária'" ? '#C27BA0':classe === "'Soja'" ? '#f5b3c8':classe === "'Cana'" ? '#db7093':classe === "'Arroz'" ? '#c71585':classe === "'Algodão (beta)'" ? '#ff69b4':classe === "'Outras Lavouras Temporárias'" ? '#f54ca9':classe === "'Lavoura Perene'" ? '#d082de':classe === "'Café'" ? '#d68fe2':classe === "'Citrus'" ? '#9932cc':classe === "'Dendê (beta)'" ? '#9065d0':classe === "'Outras Lavouras Perenes'" ? '#e6ccff':classe === "'Silvicultura'" ? '#7a5900':classe === "'Mosaico de Usos'" ? '#ffefc3':classe === "'Área não Vegetada'" ? '#d4271e':classe === "'Praia, Duna e Areal'" ? '#ffa07a':classe === "'Área Urbanizada'" ? '#d4271e':classe === "'Mineração'" ? '#9c0027':classe === "'Outras Áreas não Vegetadas'" ? '#db4d4f':classe === "'Corpo D'água'" ? '#0000FF':classe === "'Rio, Lago e Oceano'" ? '#2532e4':classe === "'Aquicultura'" ? '#091077' : getRandomColor();
        };
        const style = (feature) => {
            return {
                color: getColor(feature.properties.num_area),
                weight: 2,
                opacity: 1,
                fillOpacity: 0.7
            };
        };

        // Função para destacar uma feature ao passar o mouse sobre ela
        const highlightFeature = (e) => {
            const layer = e.target;

            layer.setStyle({
                weight: 3,
                color: '#666',
                dashArray: '',
                fillOpacity: 0.9
            });

            // Coloca a feature em primeiro plano (se estiver no caminho)
            if (!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
                layer.bringToFront();
            }
        };
        // Função para resetar o estilo após o "hover"
        const resetHighlight = (e) => {
            geojsonLayer.resetStyle(e.target);
        };

        // Função para carregar dados do CSV
        const loadChartData = (municipio, featureId) => {
            const formattedMunicipio = municipio ? normalizeMunicipioName(municipio) : '';
            const csvFile = `/assets/mapbiomas/${formattedMunicipio}/mapbiomas_${formattedMunicipio}_${featureId}.csv`;
            console.log('uso do solo caminho data: ', csvFile);

            d3.csv(csvFile).then(data => {
                // Agrupar os dados por 'year' e 'class'
                console.log(data);
                const groupedData = d3.group(data, d => d.year);
                
                const years = Array.from(groupedData.keys());
                const classTypes = Array.from(new Set(data.map(d => d.class)));
                const datasets = classTypes.map(classType => {
                    return {
                        label: `${classType}`,
                        data: years.map(year => {
                            const yearData = groupedData.get(year);
                            const classData = yearData.find(d => d.class === String(classType));
                            return classData ? +classData.area*100 : 0;
                        }),
                        fill: true,
                        backgroundColor: getGraphColor(String(classType)), // Função para gerar cores diferentes
                        borderColor: getGraphColor(String(classType)),
                        tension: 0.4,
                        stack:'stack1'
                    };
                });
                
                const chartConfig = {
                    labels: years,
                    datasets: datasets
                };

                const options = {
                    scales: {
                        x: {  // Configurações do eixo X
                            title: {
                                display: true,
                                text: 'Ano',  // Rótulo do eixo X
                                font: { size: 14 },
                            },
                        },
                        y: {  // Configurações do eixo Y
                            title: {
                                display: true,
                                text: 'Área (ha) de uso do solo',  // Rótulo do eixo Y
                                font: { size: 14 },
                            },
                        },
                    },
                    plugins: {
                        datalabels: {
                            display: false,  // Desativa a exibição dos valores nas barras
                        }
                    }
                };
                console.log('chartconfig: ',chartConfig);

                setChartData(chartConfig);
                setChartOptions(options);
                setShowModal(true);
            }).catch(err => {
                console.error('Erro ao carregar o CSV:', err);
            });
        };

        // Função ao clicar na feature
        const handleFeatureClick = (e) => {
            const feature = e.target.feature;
            const idFeature = feature.properties.id;  // ID da feature
            setFeatureId(idFeature);  // Armazena o ID da feature clicada

            // Carregar dados de uso do solo a partir do CSV correspondente
            loadChartData(municipio, idFeature);
        };

        // Função para cada feature que adiciona eventos e o tooltip
        const onEachFeature = (feature, layer) => {
            layer.on({
                mouseover: highlightFeature,  // Evento de hover
                mouseout: resetHighlight,      // Evento para resetar o estilo
                click: handleFeatureClick // Evento de clique na feature
            });

            // Adiciona o tooltip exibindo a propriedade 'num_area'
            if (feature.properties && feature.properties.num_area) {
                layer.bindTooltip(`Área: ${feature.properties.num_area} ha`, {
                    permanent: false,  // O tooltip só aparece ao passar o mouse
                    direction: "top"   // O tooltip aparecerá acima da feature
                });
            }
        };
        // Criar e adicionar a legenda ao mapa
        const legend = L.control({ position: 'bottomright' });

        legend.onAdd = function () {
            const div = L.DomUtil.create('div', 'info legend');
            const grades = [0, 50, 100, 200, 500, 1000];
            const colors = ['#FED976', '#FD8D3C', '#FC4E2A', '#E31A1C', '#BD0026', '#800026'];

            // Adicionar CSS para garantir que os quadrados de cor apareçam
            div.innerHTML = '<div style="font-size: 12px;">';
            for (let i = 0; i < grades.length; i++) {
                div.innerHTML +=
                    '<i style="background:' + colors[i] + '; width: 18px; height: 18px; border-radius: 3px; display: inline-block; margin-right: 8px;"></i> ' +
                    (grades[i] + (grades[i + 1] ? '&ndash;' + grades[i + 1] + '<br>' : '+'));
            }
            div.innerHTML += '</div>';

            return div;
        };

        legend.addTo(map);

        const updateMap = () => {
            let geojsonFile = '';
            const formattedMunicipio = municipio ? normalizeMunicipioName(municipio) : '';
            //const formattedMunicipio = normalizeMunicipioName(municipio);
            geojsonFile = `/assets/municipio_${formattedMunicipio}.geojson`;
                        
            d3.json(geojsonFile).then(geojsonData => {
                
                // Remove o GeoJSON existente antes de adicionar o novo
                map.eachLayer(layer => {
                    if (layer instanceof L.GeoJSON) {
                        map.removeLayer(layer);
                    }
                });

                geojsonLayer = L.geoJSON(geojsonData, {style: style, onEachFeature: onEachFeature}).addTo(map);
                map.fitBounds(geojsonLayer.getBounds()); // Ajusta o zoom para se ajustar aos limites do GeoJSON
            }).catch(error => {
                console.error('Erro ao carregar o arquivo GeoJSON:', error);
            });
        };

        updateMap();

        // Redimensionar o mapa ao alterar o tamanho da janela
        window.addEventListener('resize', updateMap);

        return () => {
            map.remove();
            window.removeEventListener('resize', updateMap);
        };
    }, [municipio]);

    // Função para gerar cores aleatórias para o gráfico
    const getRandomColor = () => {
        const letters = '0123456789ABCDEF';
        let color = '#';
        for (let i = 0; i < 6; i++) {
            color += letters[Math.floor(Math.random() * 16)];
        }
        return color;
    };

    return (
        <>
            <div ref={mapRef} id="map" className="map-container"></div>

            {/* Modal para exibir o gráfico */}
            <Modal show={showModal} onHide={() => setShowModal(false)} size="lg" centered>
                <Modal.Header closeButton>
                    <Modal.Title>Gráfico de Uso do Solo</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    {chartData && <Bar data={chartData} options={chartOptions}/>}
                </Modal.Body>
            </Modal>
        </>
    );
};

export default MapComponent;
