import React, { useEffect, useRef, useState } from 'react';
import Chart from 'chart.js/auto';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS, Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale } from 'chart.js';
import ChartDataLabels from 'chartjs-plugin-datalabels';
import * as d3 from 'd3';

ChartJS.register(Title, Tooltip, Legend, BarElement, CategoryScale, LinearScale, ChartDataLabels);

const getColor = (labels) => {
    //console.log('qual é a cor de: ', labels);
    return labels == 'De 10.000 ha e mais' ? '#800026' :
    labels == 'De 2.500 a menos de 10.000 ha' ? '#800026' :
    labels == 'De 1.000 a menos de 2.500 ha' ? '#800026' :
    labels == 'De 500 a menos de 1.000 ha' ? '#BD0026' :
    labels == 'De 200 a menos de 500 ha' ? '#E31A1C' :
    labels == 'De 100 a menos de 200 ha' ? '#FC4E2A' :
    labels == 'De 50 a menos de 100 ha'  ? '#FD8D3C' :
                            '#FED976';
};
// Função para criar um padrão de linhas no Canvas
function createPattern(ctx, color, patternType, colorPattern) {
    const patternCanvas = document.createElement('canvas');
    patternCanvas.width = 10;
    patternCanvas.height = 10;
    const pctx = patternCanvas.getContext('2d');

    // Desenhe a cor de fundo sólida
    pctx.fillStyle = color;
    pctx.fillRect(0, 0, 10, 10);

    // Desenhe o padrão sobre a cor de fundo
    pctx.strokeStyle = colorPattern;  // Cor do padrão
    pctx.fillStyle = colorPattern;
    if (patternType === 'diagonal-stripes') {
        pctx.beginPath();
        pctx.moveTo(0, 10);
        pctx.lineTo(10, 0);
        pctx.stroke();
    } else if (patternType === 'dots') {
        pctx.beginPath();
        pctx.arc(5, 5, 2, 0, Math.PI * 2);
        pctx.fill();
    }

    return ctx.createPattern(patternCanvas, 'repeat');
}

const ChartComponent = ({ municipio }) => {
    const chartRef = useRef(null);
    const [chartData, setChartData] = useState(null);
    const chartInstance = useRef(null);

    useEffect(() => {
        d3.dsv(';', '/assets/pr_af_area.csv').then(data => {
            const filteredData = data.filter(row => row.mun === municipio);
            const groupedData = processData(filteredData);
            groupedData.labels.map(item => console.log('label: ', item));

            const chartConfig = {
                labels: groupedData.labels,
                datasets: [
                    {
                        label: 'Agricultura Familiar',
                        data: groupedData.agriculturaFamiliar,
                        backgroundColor: function (ctx) {
                            return groupedData.labels.map(item => createPattern(ctx.chart.ctx, getColor(item), 'diagonal-stripes', '#000'));
                        },
                        yAxisID: 'y',
                        stack:'stack1',
                    },
                    {
                        label: 'Não Agricultura Familiar',
                        data: groupedData.naoAgriculturaFamiliar,
                        backgroundColor: function (ctx) {
                            return groupedData.labels.map(item => createPattern(ctx.chart.ctx, getColor(item), 'dots', '#000'));
                        },
                        yAxisID: 'y',
                        stack:'stack1',
                    },
                    {
                        label: 'Área Ocupada (ha) por Agricultura Familiar',
                        data: groupedData.areasaf,
                        backgroundColor: function (ctx) {
                            return groupedData.labels.map(item => createPattern(ctx.chart.ctx, getColor(item), 'diagonal-stripes', '#fff'));
                        },
                        yAxisID: 'y1',
                        stack:'stack2',
                    },
                    {
                        label: 'Área Ocupada (ha) por Agricultura Não Familiar',
                        data: groupedData.areasnaf,
                        backgroundColor: function (ctx) {
                            return groupedData.labels.map(item => createPattern(ctx.chart.ctx, getColor(item), 'dots', '#fff'));
                        },
                        yAxisID: 'y1',
                        stack:'stack2',
                    }
                ]
            };

            setChartData(chartConfig);
        });
    }, [municipio]);

    useEffect(() => {
        if (chartData && chartRef.current) {
            if (chartInstance.current) {
                chartInstance.current.destroy(); // Destruir o gráfico existente
            }

            const ctx = chartRef.current.getContext('2d');
            chartInstance.current = new Chart(ctx, {
                type: 'bar',
                data: chartData,
                options: {
                    responsive: true,
                    plugins: {
                        datalabels: {
                            display: false,  // Desativa a exibição dos valores nas barras
                        }
                    },
                    scales: {
                        x: {
                            stacked: true
                        },
                        y: {
                            stacked: true,
                            beginAtZero: true,
                            title: {
                                display: true,
                                text: 'Número de Propriedades'
                            }
                        },
                        y1: {
                            beginAtZero: true,
                            position: 'right',  // Coloca a segunda escala y na direita
                            title: {
                                display: true,
                                text: 'Área Ocupada (ha)'
                            },
                            grid: {
                                drawOnChartArea: false, // Impede que as linhas da grade da y1 apareçam sobre o gráfico principal
                            },
                        }
                    }
                }
            });
        }
    }, [chartData]);

    const processData = (data) => {
        const labels = [];
        const agriculturaFamiliar = [];
        const naoAgriculturaFamiliar = [];
        const areasaf= [];
        const areasnaf= [];

        data.forEach(row => {
            if (!labels.includes(row.grupos)) {
                labels.push(row.grupos);
                agriculturaFamiliar.push(0);
                naoAgriculturaFamiliar.push(0);
                areasaf.push(0);
                areasnaf.push(0);
            }

            const index = labels.indexOf(row.grupos);
            agriculturaFamiliar[index] += +row.n_af_sim;
            naoAgriculturaFamiliar[index] += +row.n_af_nao;
            areasaf[index] += +row.area_af_sim; 
            areasnaf[index] += +row.area_af_nao; 
        });

        return { labels, agriculturaFamiliar, naoAgriculturaFamiliar, areasaf, areasnaf };
    };

    return <canvas ref={chartRef}></canvas>;
};

export default ChartComponent;
